"use client";

import { useState } from "react";
import { Map, Marker, Popup } from "react-map-gl/maplibre";
import "maplibre-gl/dist/maplibre-gl.css";
import ReportPopup from "./components/ReportPopup";

const page = () => {
  const [longitude, setLongitude] = useState(-73.9822);
  const [latitude, setLatitude] = useState(40.7685);
  const [showPopup, setShowPopup] = useState(true);

  const handleDragEnd = (e: any) => {
    const { lng, lat } = e.lngLat;
    setLongitude(lng);
    setLatitude(lat);
  };

  return (
    <div className="h-screen w-screen">
      <div className="position-absolute bg-slate-500 left-0 top-0 w-100% h-14">
        <p className="text-white">longitude: {longitude}</p>
        <p className="text-white">latitude: {latitude}</p>
      </div>
      <Map
        initialViewState={{
          longitude: -73.9822,
          latitude: 40.7685,
          zoom: 14,
        }}
        style={{ width: "100%", height: "100%", position: "relative" }}
        mapStyle={`https://api.maptiler.com/maps/streets/style.json?key=${process.env.NEXT_PUBLIC_MAPTILER_KEY}`}
      >
        <Marker
          longitude={longitude}
          latitude={latitude}
          anchor="bottom"
          scale={1}
          draggable={true}
          onDragEnd={handleDragEnd}
          onClick={() => setShowPopup((prev) => !prev)}
        />
        {showPopup && (
          <Popup
            longitude={longitude}
            latitude={latitude}
            closeButton={true}
            closeOnClick={false}
            onClose={() => setShowPopup(false)}
            anchor="bottom"
            offset={60}
          >
            <ReportPopup latitude={latitude} longitude={longitude} />
          </Popup>
        )}
      </Map>
    </div>
  );
};

export default page;
