"use client";

import { useState, useEffect } from "react";

interface ReportType {
  id: number;
  name: string;
}

interface PopupFormProps {
  latitude: number;
  longitude: number;
  reportTypes: ReportType[];
  onSubmit: (payload: {
    reportTypeId: number;
    description: string;
    lat: number;
    long: number;
  }) => void;
  onClose: () => void;
}

const PopupForm = ({
  latitude,
  longitude,
  reportTypes,
  onSubmit,
  onClose,
}: PopupFormProps) => {
  const [selectedReportType, setSelectedReportType] = useState<number>(
    reportTypes.length > 0 ? reportTypes[0].id : 0
  );
  const [description, setDescription] = useState("");

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!selectedReportType) {
      return;
    }

    const payload = {
      reportTypeId: selectedReportType, // Admin can change these options
      description, // Optional description
      lat: latitude, // Current latitude
      long: longitude, // Current longitude
      // Additional fields (like initial trustScore) can be set server-side.
    };

    onSubmit(payload);
    // Optionally clear the form if desired:
    setDescription("");
  };

  return (
    <div className="">
      <form onSubmit={handleSubmit} className="p-2">
        <div>
          <label htmlFor="reportType" className="block font-bold">
            Report Type:
          </label>
          <select
            id="reportType"
            value={selectedReportType}
            onChange={(e) => setSelectedReportType(Number(e.target.value))}
            required
            className="border rounded p-1 w-full"
          >
            {reportTypes.map((type) => (
              <option key={type.id} value={type.id}>
                {type.name}
              </option>
            ))}
          </select>
        </div>
        <div className="mt-2">
          <label htmlFor="description" className="block font-bold">
            Description:
          </label>
          <textarea
            id="description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            className="border rounded p-1 w-full"
          />
        </div>
        <div className="mt-2">
          <p>
            <strong>Latitude:</strong> {latitude}
          </p>
          <p>
            <strong>Longitude:</strong> {longitude}
          </p>
        </div>
        <div className="mt-2 flex space-x-2">
          <button
            type="submit"
            className="bg-blue-500 text-white px-3 py-1 rounded"
          >
            Submit Report
          </button>
          <button
            type="button"
            className="bg-gray-500 text-white px-3 py-1 rounded"
            onClick={onClose}
          >
            Cancel
          </button>
        </div>
      </form>
    </div>
  );
};

export default PopupForm;
