// app/ReportPopup.tsx
import PopupForm from "./PopupForm";
import { getReportTypes } from "../../actions/getReportTypes";

// This is a server component so we can await the server action.
export default async function ReportPopup({
  latitude,
  longitude,
  onSubmit,
  onClose,
}: {
  latitude: number;
  longitude: number;
  onSubmit: (payload: any) => void;
  onClose: () => void;
}) {
  const reportTypes = await getReportTypes();

  return (
    <PopupForm
      latitude={latitude}
      longitude={longitude}
      reportTypes={reportTypes}
      onSubmit={onSubmit}
      onClose={onClose}
    />
  );
}
